.counter-btn {
  width: 90px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.counter-btn:after {
  content: " ";
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #1fd1f9;
  background-image: linear-gradient(315deg, #1fd1f9 0%, #b621fe 74%);
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.counter-btn:hover {
  color: #fff;
  background: none;
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .5), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .5), inset 4px 4px 6px rgba(116, 125, 136, .3);
}

.counter-btn:hover:after {
  transform: scale(2)rotate(180deg);
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .5), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .5), inset 4px 4px 6px rgba(116, 125, 136, .3);
