.magic-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: linear-gradient(0deg, #ff9700 0%, #fb4b02 100%);
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 0;
  font-family: Lato, sans-serif;
  font-weight: 500;
  line-height: 42px;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.magic-btn span {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.magic-btn:before, .magic-btn:after {
  content: "";
  background: #fb4b02;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  right: 0;
  box-shadow: -7px -7px 20px rgba(255, 255, 255, .9), -4px -4px 5px rgba(255, 255, 255, .9), 7px 7px 20px rgba(0, 0, 0, .2), 4px 4px 5px rgba(0, 0, 0, .3);
}

.magic-btn:before {
  height: 0%;
  width: 2px;
}

.magic-btn:after {
  width: 0%;
  height: 2px;
}

.magic-btn:hover {
  color: #fb4b02;
  background: none;
}

.magic-btn:hover:before {
  height: 100%;
}

.magic-btn:hover:after {
  width: 100%;
}

.magic-btn span:before, .magic-btn span:after {
  content: "";
  background: #fb4b02;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: -7px -7px 20px rgba(255, 255, 255, .9), -4px -4px 5px rgba(255, 255, 255, .9), 7px 7px 20px rgba(0, 0, 0, .2), 4px 4px 5px rgba(0, 0, 0, .3);
}

.magic-btn span:before {
  width: 2px;
  height: 0%;
}

.magic-btn span:after {
  height: 2px;
  width: 0%;
}

.magic-btn span:hover:before {
  height: 100%;
}

.magic-btn span:hover:after {
  width: 100%;
