
.magic-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
  background: linear-gradient(0deg, rgba(255,151,0,1) 0%, rgba(251,75,2,1) 100%);
    line-height: 42px;
    padding: 0;
    border: none;
  }
  .magic-btn span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
  }
  .magic-btn:before,
  .magic-btn:after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 0;
    background: rgba(251,75,2,1);
    box-shadow:
     -7px -7px 20px 0px rgba(255,255,255,.9),
     -4px -4px 5px 0px rgba(255,255,255,.9),
     7px 7px 20px 0px rgba(0,0,0,.2),
     4px 4px 5px 0px rgba(0,0,0,.3);
    transition: all 0.3s ease;
  }
  .magic-btn:before{
     height: 0%;
     width: 2px;
  }
  .magic-btn:after {
    width: 0%;
    height: 2px;
  }
  .magic-btn:hover{
    color: rgba(251,75,2,1);
    background: transparent;
  }
  .magic-btn:hover:before {
    height: 100%;
  }
  .magic-btn:hover:after {
    width: 100%;
  }
  .magic-btn span:before,
  .magic-btn span:after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    background: rgba(251,75,2,1);
    box-shadow:
     -7px -7px 20px 0px rgba(255,255,255,.9),
     -4px -4px 5px 0px rgba(255,255,255,.9),
     7px 7px 20px 0px rgba(0,0,0,.2),
     4px 4px 5px 0px rgba(0,0,0,.3);
    transition: all 0.3s ease;
  }
  .magic-btn span:before {
    width: 2px;
    height: 0%;
  }
  .magic-btn span:after {
    height: 2px;
    width: 0%;
  }
  .magic-btn span:hover:before {
    height: 100%;
  }
  .magic-btn span:hover:after {
    width: 100%;
  }